.newposttitleline {
  height: 2px;
  width: 10vw;
  display: block;
}

.newposttitleflex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: none;
      flex-wrap: none;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
}

@media screen and (max-width: 850px){
.newpostdreamnotesfont {
  font-size:30px;
}
.titletext {
  width: 75vw;
  font-size: 10pt;
}
.storytext {
  min-height: 10%;
  font-size: 10pt;
  width: 75vw;
}
.influencetext {
  height: 40%;
  font-size: 10pt;
  width: 36vw;
}
.influencespandivs {
  width: 40vw;
  font-size: 10pt;
  min-height: 40%;
}
.spantext {
 font-size: 10pt;
}


}


